.about {
  background-image: linear-gradient(bisque 5%, teal 75%);
  color: bisque;
  min-height: 95vh;
}

.about p:last-child {
  margin-bottom: 0;
}

.photo {
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 15%;
  border-radius: 50%;
}
